import React from 'react';
import styles from './MyPosts.module.scss';
import Post from './Post/Post';
import cn from "classnames";
import defaultAvatar from "../../../defaultAvatar.png"
import { Form, Field } from 'react-final-form'
import { composeValidators, requiredField, maxLenghtCreator } from "./../../../utils/validators"
import { Textarea } from '../../common/formsControls/FormsControls';
import Info from '../../common/Info/Info';
import { connect } from 'react-redux';
import { addPost } from '../../../redux/profile-reducer';
import { getPosts } from '../../../redux/profile-selectors';

const maxLenght = maxLenghtCreator(250);

const AddPostForm = (props) => {
    return (
        <Form
            onSubmit={props.onSubmit}
        >
            {
                ({handleSubmit}) => (
                    <form onSubmit={handleSubmit} className={styles.form}>
                        <Field
                            component={Textarea} 
                            name="newPostbody"
                            placeholder="Enter post text here..." 
                            validate={composeValidators(requiredField, maxLenght)}
                            className={cn(styles.postText, "entry_field")} 
                        />
                        <button className={cn(styles.addPostBtn, "submit_btn")}>Add post</button>
                    </form>
                )
            }
        </Form>
    )
}

const MyPosts = React.memo(props => {
    let avatar = props.profile.photos.large || defaultAvatar;

    const addPost = (values) => {
        props.addPost(avatar, values.newPostbody);
    }

    return (
        <section className={styles.myPosts}>
            <Info text={"This is a test component. Current version of API not supporting this functional yet"}/>
            <h3 className={styles.header}>Add new post:</h3>
            <AddPostForm onSubmit={addPost}/>
            <h4 className={styles.header}>Recent Posts:</h4>
            <ul>
                { props.posts.map(post => <Post message={post.postText} key={post.id} imgUrl={avatar}/>) }
            </ul>
        </section>
    )
})

let mapStateToProps = state => {
    return {
        posts: getPosts(state)
    }
}

const MyPostsContainer = connect(mapStateToProps, { addPost })(MyPosts)

export default MyPostsContainer;