import { useEffect, useState } from 'react';
import styles from "./ProfileInfo.module.scss";

const ProfileStatusWithHooks = (props) => {
   
    let [editMode, setEditMode] = useState(false);
    let [status, setStatus] = useState(props.status);

    const activateEditMode = () => {
        if(props.isOwner) {
            setEditMode(true);
        } 
    },
    deactivateEditMode = () => {
        setEditMode(false);
        props.updateStatus(status)
    },
    onStatusChange = (e) => {
        setStatus(e.target.value);
    };

    useEffect( () => {
        setStatus(props.status);
    }, [props.status] )


    return (
        <div className={styles.statusContainer}>
            {!editMode ?
                (
                    <div>
                        <span className={!props.status ? styles.noStatus : null } onDoubleClick={activateEditMode}>{ props.status || "Set status... (double click here)" }</span>
                    </div>
                ) : 
                (
                    
                    <div>
                        <textarea className={styles.newStatusInput} onChange={onStatusChange} autoFocus={true} onBlur={deactivateEditMode} value={status} />
                    </div>  
                )
            }
        </div>
    )
}

export default ProfileStatusWithHooks;